        .data_popup {
          position: absolute;
          left: 10px;
          top: 50px;
          bottom: 50px;
          overflow: hidden;
          z-index: 999;
          background-color: $bg2;
          border-radius: 5px;
          max-width: 250px;
          width: 100%;
          height: auto;
    
          .dcont {
            width: 100%;
            height: 100%;
            overflow-y: auto;
            padding: 10px;
            position: relative;
    
            .fa-close {
              position: absolute;
              right: 10px;
              bottom: 10px;
              display: block;
              margin: 0 0 0 auto;
              color: $accent;
              cursor: pointer;
              width: fit-content;
            }
    
            .fa-close:hover {
              color: #74465c;
            }
          }
          overflow-y: auto;

          .workspace_item {
            margin-bottom: 5px;
    
            .top {
              display: grid;
              grid-template-columns: 1fr auto;
              padding: 5px;
              background-color: wheat;
              border-radius: 2px;
              cursor: pointer;
    
              p {
                font-size: small;
                display: block;
                margin: auto;
                text-align: left;
                width: 100%;
              }
    
              i {
                font-size: medium;
                display: block;
                margin: auto;
              }
            }
    
            .top:hover {
              background-color: whitesmoke;
            }
    
            .item {
              cursor: pointer;
              display: grid;
              grid-template-columns: auto 1fr;
              gap: 10px;
              padding: 5px;
              border-radius: 2px;
              margin: 3px;
              width: 90%;
              p {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                font-size: small;
                margin: auto;
                text-align: left;
                width: 100%;
                clear: both;
                display: inline-block;
                overflow: hidden;
                white-space: wrap;
              }
    
              i {
                font-size: small;
                display: block;
                margin: auto;
                color: $accent;
              }
            }
    
            .item:hover {
              background-color: wheat;
            }
          }
    
          .bar {
            background-color: $accent;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            border-radius: 5px 5px 0 0;
    
            p {
              font-size: small;
              padding: 4px 6px 4px 6px !important;
              color: white;
              text-align: center;
              cursor: pointer;
            }
    
            p:hover {
              color: $accent;
              background-color: $bg2;
              border-radius: 5px 5px 0 0;
            }
    
            .active {
              color: $accent;
              background-color: $bg2;
              cursor: pointer;
              border-radius: 5px 5px 0 0;
            }
          }
    
          .dat_cont {
            padding: 1em;
          }
    
          .basic_styler {
            padding: 10px;
            position: relative;
    
            .query {
              width: 100% !important;
              height: fit-content !important;
              padding: 4px !important;
              font-size: small;
              margin: auto;
              display: block;
              border: 1px solid $accent !important;
              border-radius: 5px;
              outline: none;
            }
    
            button {
              padding: 6px 1em 6px 1em;
              background-color: $accent;
              color: white;
              margin: 12px auto 0 auto;
              border: none;
              border-radius: 5px;
              cursor: pointer;
              display: block;
              min-width: 100px;
    
              .fa-check {
                color: white;
              }
            }
    
            .range_popup {
              position: absolute;
              top: -24px;
              bottom: auto;
              left: 240px;
              right: 0;
              display: flex;
              align-items: center;
              width: 100%;
              max-width: 250px;
    
              .container {
                background-color: white;
                border-radius: 5px;
                padding: 10px;
                width: 100%;
                margin: auto 1em auto 1em;
                box-shadow: 3px 3px 5px #60606040;
    
                h3 {
                  font-weight: 600 !important;
                }
    
                button {
                  padding: 6px 1em 6px 1em;
                  background-color: $accent;
                  color: white;
                  margin: 12px auto 0 auto;
                  border: none;
                  border-radius: 5px;
                  cursor: pointer;
                  display: block;
                  min-width: 100px;
    
                  .fa-check {
                    color: white;
                  }
                }
    
                .columns {
                  display: grid;
                  grid-template-columns: 1fr 0.5fr 0.5fr;
                  gap: 5px;
                  margin-bottom: 8px;
    
                  input {
                    width: 100%;
                    padding: 2px !important;
                    font-size: small;
                  }
                }
              }
            }
    
            .buttons {
              width: fit-content;
              margin: 5px 0 5px auto;
              display: grid;
              grid-template-columns: repeat(2, auto);
              gap: 10px;
    
              .fa {
                cursor: pointer;
                color: $accent;
                font-size: medium;
                padding: 3px;
              }
    
              .fa:hover {
                color: #74465c;
              }
            }
    
            input[type="number"] {
              padding: 8px 10px 8px 10px !important;
              border: 1px solid $accent !important;
              border-radius: 5px;
              color: $accent !important;
              min-width: 44px;
              text-align: center;
            }
    
            p {
              color: $accent;
              font-size: small;
            }
    
            .classes {
              max-height: 28vh;
              height: 100%;
              overflow-y: auto;
            }
    
            .fa {
              color: $accent;
              padding: 3px;
            }
    
            .row {
              display: grid;
              grid-template-columns: auto 1fr auto auto;
              gap: 10px;
              margin: 5px 0 5px 0;
    
              input {
                width: 16px;
                height: 16px;
                border: none;
                cursor: pointer;
                display: block;
                margin: auto;
              }
    
              p {
                font-size: small;
                display: block;
                width: 100%;
                margin: auto;
              }
    
              select {
                padding: 3px;
                background-color: $bg2;
                border: 1px solid $accent;
                border-radius: 3px;
                cursor: pointer;
                font-size: smaller;
                color: $accent;
              }
            }
          }
    
          .split {
            display: grid;
            grid-template-columns: auto auto !important;
    
            input {
              width: 24px;
            }
          }
    
          h3 {
            font-size: small;
            font-weight: 500;
            color: black;
            margin: 7px 0 5px 0;
          }
    
          br {
            height: 1px !important;
            padding: 0;
            margin: 0;
          }
    
          hr {
            color: #74465c;
            margin: 5px 0 10px 0;
          }
        }
  }

  .cont {
    padding: 2em;
    background-color: white;
    border-radius: 5px;
    margin: 1em;

    .input-map {
      margin: 10px 0 10px 0;

      h4 {
        display: block;
        margin: 10px 0 10px 0;
        font-weight: 400;
      }
      textarea {
        margin: 0px 0 10px 0;
        padding: 10px;
        width: 100%;
        background-color: #60606030;
        border-top: none;
        border-right: none;
        border-left: none;
        outline: none;
        border-bottom: 2px solid #60606070;
      }
      textarea:focus {
        border-bottom: 2px solid $primary;
      }
      input {
        margin: 0px 0 10px 0;
        padding: 10px;
        width: 100%;
        background-color: #60606030;
        border-top: none;
        border-right: none;
        border-left: none;
        outline: none;
        border-bottom: 2px solid #60606070;
      }
      input:focus {
        border-bottom: 2px solid $primary;
      }
    }

    .div2equal {
      gap: 2em !important;
      margin: 2em 0 2em 0;
    }

    .img {
      position: relative;
      width: 100%;
      height: calc(25vw * 0.6);

      img {
        width: 100%;
        height: 100%;
        border-radius: 5px;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: contain;
        object-position: center;
        border-radius: 5px;
      }

      input {
        display: none;
      }

      i {
        height: 40px;
        width: 40px;
        text-align: center;
        line-height: 40px;
        left: 45%;
        position: absolute;
        bottom: -20px;
        cursor: pointer;
        background-color: $accent;
        border-radius: 5px;
      }
      i:hover {
        background-color: $dark;
        color: $accent;
      }

      @include maxwidth(tablet) {
        height: 200px !important;
      }
    }

    h3 {
      font-size: 18px;
      line-height: 1.3;
      max-width: 80%;
      color: $primary;
      text-align: center;
      display: block;
      margin: 2em auto 10px auto;
    }

    p {
      font-size: 14px;
      line-height: 1.3;
      max-width: 60%;
      display: block;
      margin: 10px auto 10px auto;
      text-align: center;
    }
  }

  @include maxwidth(mobile) {
    margin-top: 1px !important;
    padding-bottom: 18% !important;
    .model {
      .container {
        max-width: 80vw !important;
      }
    }
    .map {
      .leaflet-container {
        height: 60vh !important;
      }
      .sidepanel {
        .Popup {
          height: 44vh !important;
          width: 90vw !important;
        }
      }
    }
  }

  @include maxwidth(tablet) {
    margin-top: 1px !important;
    padding-bottom: 18% !important;
    .model {
      .container {
        max-width: 80vw !important;
      }
    }
    .map {
      .leaflet-container {
        height: 60vh !important;
      }
      .sidepanel {
        .Popup {
          height: 44vh !important;
          width: 50% !important;
        }
      }
    }
  }
}
